<template>
  <el-card class="main personal-center" shadow="true">
    <div slot="header" class="header clearfix">
      <div class="parent-nav">
        <el-button
          v-for="(item, index) in parentNav"
          :key="index"
          round
          size="medium"
          :class="activeParentNav === index ? 'button-nav-active' : ''"
          @click="setSubNav(index)"
          >{{ item.text }}</el-button
        >
      </div>
      <div v-if="subNavAvilabel" class="sub-nav">
        <el-link
          v-for="(item, index) in currentSubNav"
          :key="index"
          :underline="false"
          :href="item.href"
          :class="item.index === activeSubNav ? 'button-nav-active' : ''"
          >{{ item.text }}</el-link
        >
      </div>
    </div>
    <div class="body">
      <content-template :path="path" />
    </div>
  </el-card>
</template>
<script>
import router from "../router";
import contentTemplate from "./personal-center/contentTemplate";
import { mapActions } from "vuex";

export default {
  name: "personal-center",
  components: { contentTemplate },
  data() {
    return {
      activeParentNav: 0,
      activeSubNav: "0-0",
      subNavAvilabel: true,
      path: "",
      parentNav: [
        { text: this.$t("personalCenter.myCenter"), href: "#/user" },
        { text: this.$t("personalCenter.myFinancial"), href: "#/financial" },
        { text: this.$t("personalCenter.myAnnouncetment"), href: "#/notice" }
        //{ text: this.$t("personalCenter.download"), href: "#/download" }
      ],
      subNav: [
        [
          {
            text: this.$t("personalCenter.myAccount"),
            href: "#/user/account",
            index: "0-0"
          },
          {
            text: this.$t("personalCenter.myBankCard"),
            href: "#/user/bank",
            index: "0-1"
          },
          // {
          //   text: this.$t("personalCenter.myLimited"),
          //   href: "#/user/limit",
          //   index: "0-2"
          // },
          // {
          //   text: this.$t("personalCenter.myOrder"),
          //   href: "#/user/order",
          //   index: "0-3"
          // },
          {
            text: this.$t("personalCenter.myBet"),
            href: "#/user/bets",
            index: "0-4"
          },
          {
            text: this.$t("gameTemplate.trendChart"),
            href: "#/user/trend-chart",
            index: "0-11"
          },
          {
            text: this.$t("personalCenter.myTask"),
            href: "#/user/tasks",
            index: "0-5"
          },
          // {
          //   text: this.$t("personalCenter.myMessage"),
          //   href: "#/user/messages",
          //   index: "0-6"
          // },
          // {
          //   text: this.$t("personalCenter.myLoginLog"),
          //   href: "#/user/login-log",
          //   index: "0-7"
          // },
          {
            text: this.$t("personalCenter.AmountChangingRecord"),
            href: "#/user/amount-changing-record",
            index: "0-8"
          },
          {
            text: this.$t("personalCenter.IndividualStatistics"),
            href: "#/user/individual-statistics",
            index: "0-9"
          },
          {
            text: this.$t("personalCenter.DragonTigerTie"),
            href: "#/user/dragon-tiger-tie",
            index: "0-10"
          }
        ],
        [
          {
            text: this.$t("personalCenter.deposits"),
            href: "#/financial/deposits",
            index: "1-0"
          },
          {
            text: this.$t("personalCenter.withdrawals"),
            href: "#/financial/withdrawals",
            index: "1-1"
          }
          // {
          //   text: this.$t("personalCenter.depositRecords"),
          //   href: "#/financial/deposit-record",
          //   index: "1-2"
          // },
          // {
          //   text: this.$t("personalCenter.withdrawalRecords"),
          //   href: "#/financial/withdrawal-record",
          //   index: "1-3"
          // }
        ]
      ],
      currentSubNav: []
    };
  },
  computed: {},
  methods: {
    ...mapActions(["getBankCardList"]),
    setSubNav(index) {
      this.activeParentNav = index;
      if (index === 0 || index === 1) {
        this.subNavAvilabel = true;
        this.currentSubNav = this.subNav[index];
      } else if (index === 2) {
        this.activeSubNav = "100-100";
        this.subNavAvilabel = false;
        router.push({ path: "/notice" });
      } else if (index === 3) {
        router.push({ path: "/download" });
      }
    },
    setNavState(path) {
      this.path = path;
      this.subNavAvilabel = true;

      let array = this.path.split("/").slice(1);
      this.parentNav.forEach((item, index) => {
        if (item.href.split("/")[1] === array[0]) {
          this.activeParentNav = index;

          if (index < 2) {
            this.currentSubNav = this.subNav[index];
            this.currentSubNav.forEach((subItem, subIndex) => {
              if (subItem.href.split("/")[2] === array[1]) {
                this.activeSubNav = subItem[index];
              }
            });
          } else if (index === 2) {
            this.subNavAvilabel = false;
          }
        }
      });
    }
  },
  watch: {
    $route(to, from) {
      this.setNavState(to.path);
    }
  },
  created() {
    this.setNavState(router.history.current.path);
    this.getBankCardList();
  }
};
</script>

<style lang="sass">
@import "../common/css/color.sass";

.clearfix:before,
.clearfix:after
    display: table
    content: ""

%nav-middle
  height: 50px
  display: flex
  align-items: center
%nav-button
  background-color: transparent
  color: #fff
  box-shadow: none
  border: 0
  height: 35px

.page-index
  .main.personal-center
    margin-top: 50px
.personal-center
  //margin-top: 20px
  background-color: transparent
  border: 0
  >div
    padding: 0
  .header
    .parent-nav
      background-color: var(--primary)
      border-radius: 10px 10px 0px 0px
      @extend %nav-middle
      button
        margin-left: 10px
        @extend %nav-button
        &.button-nav-active
         background-color: var(--selected)
    .sub-nav
      background-color: $view-background-color
      @extend %nav-middle
      a
        color: #333
        margin-left: 10px
        @extend %nav-button
        &:after
          content: "|"
          padding-left: 10px
        &:last-child
          &:after
            content: ""
        &.button-nav-active
         color: var(--selected)
  .body
    background-color: #fff
    border: solid 1px #fff
    padding: 20px 15px


//帮助区域的样式
.personal-center
  .body
    .help-title
      font-size: 19px
      color: #333333
      margin: 20px 0
      font-weight: 500
    .sub-help
      padding: 20px 0
      border-top: 1px solid #eee
      p
        padding-bottom: 4px
        font-size: 13px
        font-weight: 500
        line-height: 1.5em

//服务器端的错误信息
.server-error
  display: block
  color: #ff0000
  padding: 10px 0px
</style>
