<template>
  <div class="personal-center-content">
    <keep-alive :max="20">
      <component :is="currentContent"></component>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "personal-center-content-template",
  props: ["path"],
  components: {
    userAccount: () => import("./myAccount"),
    userBank: () => import("./bankCard"),
    userLimit: () => import("./limitSetting"),
    userOrder: () => import("./myOrder"),
    userBets: () => import("./myBet"),
    userTasks: () => import("./myTask"),
    userMessages: () => import("./myMessage"),
    userLoginLog: () => import("./myLoginLog"),
    financialDeposits: () => import("./deposit"),
    financialWithdrawals: () => import("./withdraw"),
    financialDepositRecord: () => import("./depositRecord"),
    financialWithdrawalRecord: () => import("./withdrawalRecord"),
    notice: () => import("./notice"),

    userAmountChangingRecord: () => import("./amount-changing-record"),
    userIndividualStatistics: () => import("./individual-statistics"),
    userDragonTigerTie: () => import("./dragon-tiger-tie"),
    userTrendChart: () => import("./trend-chart")
  },
  computed: {
    currentContent() {
      var component = "";
      this.path
        .replace("#", "")
        .split(/\/|-/)
        .forEach((item, index) => {
          component +=
            item !== "" && index > 1
              ? item
                  .toLowerCase()
                  .charAt(0)
                  .toUpperCase() + item.substr(1)
              : item.toLowerCase();
        });

      console.log(component, "动态加载的组件名");
      return component;
      /*
      #/user/account
      #/user/bank
      #/user/limit
      #/user/order
      #/user/bets
      #/user/tasks
      #/user/messages
      #/user/login-log
      #/financial/deposits
      #/financial/withdrawals
      #/financial/deposit-record
      #/financial/withdrawal-record
      #//user/amount-changing-record
      #//user/individual-statistics
      #//user/dragon-tiger-tie
      #/user/trend-chart
      */
    }
  }
};
</script>
